import React from "react";
import ApiManager from "../service/store-service";
import { setNewAccess } from "../service/jwt";

const JWTWrapper = (View) => {
  return class extends React.Component {
    api = new ApiManager();

    checkData() {
      const access = localStorage.getItem("jwtToken");
      const refresh = localStorage.getItem("refreshToken");
      this.api.checkToken(access, refresh).then((result) => {
        if (result === true) {
          this.props.USER_IS_LOGGED();
        } else if (result === false) {
          this.props.USER_IS_NOT_LOGGED();
        } else {
          setNewAccess(result.data.access);
          this.props.USER_IS_LOGGED();
        }
      });
    }

    componentDidMount() {
      this.checkData();
      setInterval(() => {
        this.checkData();
      }, 1000 * 5);
    }
    render() {
      const access = localStorage.getItem("jwtToken").replace(/"/g, "");
      const refresh = localStorage.getItem("refreshToken").replace(/"/g, "");
      return <View {...this.props} access={access} refresh={refresh} />;
    }
  };
};

export default JWTWrapper;
