import React, { Component } from "react";
import Slider from "react-slick";
import { withRouter } from "react-router-dom";
import "./style.css";

class ProductCard extends Component {
  SelectedItem = (id) => {
    this.props.history.push(`/products/${id}`);
  };

  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
    const { data } = this.props;

    return (
      <div className="card col-md-3">
        <div className="product-image">
          <Slider {...settings}>
            {data.images.map((item) => (
              <img
                src={`${item.image}`}
                className="card-img-top "
                alt="..."
                key={item}
              />
            ))}
          </Slider>
        </div>
        <div className="card-body">
          <h5 className="card-title">{data.title}</h5>
          <p className="card-text">Категория: {data.category}.</p>
          <p className="card-text">Доступно: {data.available}.</p>
          <p className="card-text">Цена: {data.price}.</p>
          <button
            href="#"
            className="btn btn-primary check-product-page"
            onClick={() => this.SelectedItem(data.pk)}
          >
            Go somewhere
          </button>
        </div>
      </div>
    );
  }
}

export default withRouter(ProductCard);
