import React from "react";
import { Redirect, withRouter } from "react-router-dom";
import ApiManager from "../../../service/store-service";

import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import {
  USER_IS_LOGGED,
  USER_IS_NOT_LOGGED,
  USER_LOADED_FAIL,
  USER_LOADED_SUCCESS,
  getProfileData,
} from "../../actions/actions";
import JWTWrapper from "../../container";

class Profile extends React.Component {
  server = new ApiManager();

  componentDidMount() {
    this.server
      .getProfile(this.props.access, this.props.userId)
      .then((result) => {
        this.props.getProfileData(result);
      });
  }

  render() {
    const { isLogged, profile } = this.props;
    if (isLogged) {
      return (
        <div className="container">
          <div className="row">
            <div className="col-sm-3"></div>
            <div className="col-sm-9">
              <ul className="list-group">
                Имя:
                <input value={profile.username} />
                Телефон:
                <input value={profile.phone} />
                E-mail:
                <input value={profile.email} />
                Количество заказов:{profile.orders_quantity}
              </ul>
            </div>
          </div>
        </div>
      );
    }
    return <Redirect to="/login" />;
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators(
    {
      USER_LOADED_FAIL,
      USER_LOADED_SUCCESS,
      USER_IS_LOGGED,
      USER_IS_NOT_LOGGED,
      getProfileData,
    },
    dispatch
  );
};

const putStateToProps = (state) => {
  return {
    isLoaded: state.Auth.isLoaded,
    isLogged: state.Auth.isLogged,
    profile: state.Auth.profileData,
  };
};

export default connect(
  putStateToProps,
  putActionToProps
)(withRouter(JWTWrapper(Profile)));
