import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { Link, Redirect } from "react-router-dom";

import {
  USER_LOADED_SUCCESS,
  setJWTToken,
  USER_IS_LOGGED,
} from "../actions/actions";
import { JWTAuth } from "../../service/jwt";

import "./login-style.css";
class Login extends Component {
  state = {
    username: "",
    password: "",
  };

  HandleSubmit(e) {
    e.preventDefault();
    const { username, password } = this.state;
    JWTAuth({ username: username, password: password });
    const access = localStorage.getItem("jwtToken");
    const refresh = localStorage.getItem("refreshToken");
    this.props.setJWTToken({ access, refresh });
    setTimeout(() => {
      this.props.USER_IS_LOGGED();
      this.props.USER_LOADED_SUCCESS();
    }, 500);
  }

  render() {
    if (this.props.isLogged) {
      return <Redirect to="/" />;
    }
    return (
      <div className="wrapper fadeInDown">
        <div id="formContent">
          <div className="fadeIn first">
            <img
              src="http://danielzawadzki.com/codepen/01/icon.svg"
              id="icon"
              alt="User Icon"
            />
          </div>

          <input
            type="text"
            id="login"
            className="fadeIn second"
            name="login"
            placeholder="login"
            onChange={(e) => this.setState({ username: e.target.value })}
          />
          <input
            type="text"
            id="password"
            className="fadeIn third"
            name="login"
            placeholder="password"
            onChange={(e) => this.setState({ password: e.target.value })}
          />

          <input
            type="submit"
            className="fadeIn fourth"
            value="Log In"
            onClick={(e) => this.HandleSubmit(e)}
          />

          <div id="formFooter">
            <Link className="underlineHover">Forgot Password?</Link>
          </div>
        </div>
      </div>
    );
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators(
    { USER_LOADED_SUCCESS, setJWTToken, USER_IS_LOGGED },
    dispatch
  );
};

const putStateToProps = (state) => {
  return { isLogged: state.Auth.isLogged };
};

export default connect(putStateToProps, putActionToProps)(Login);
