import React from "react";
import { Link } from "react-router-dom";
import "./style.css";
import { getUserData, USER_IS_LOGGED } from "../actions/actions";
import axios from "axios";
import { bindActionCreators } from "redux";
import { withRouter } from "react-router-dom";

import CategoryList from "../cat-list/cat-list";
import { connect } from "react-redux";

class NavBar extends React.Component {
  state = {
    dropdown: false,
  };

  getCurrentUser = async (access) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${access}`,
        Accept: "application/json",
      },
    };
    await axios
      .get("http://127.0.0.1:8000/auth/users/me/", config)
      .then((result) => {
        this.props.getUserData(result.data);
      });
  };

  onProfilePage = (id) => {
    let href = "/login";
    if (this.props.isLogged === true) {
      href = `/profile/${id}`;
    }
    this.props.history.push(href);
  };

  componentDidUpdate(prevProps) {
    if (prevProps !== this.props.isLogged) {
      const access = localStorage.getItem("jwtToken").replace(/"/g, "");
      this.getCurrentUser(access);
    }
  }
  render() {
    const { username } = this.props;
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <Link to="/">
          <span className="navbar-brand">Магазин</span>
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mr-auto">
            <li className="nav-item dropdown">
              <span
                className="nav-link dropdown-toggle"
                id="navbarDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                Dropdown
              </span>
              <div className="dropdown-menu" aria-labelledby="navbarDropdown">
                <CategoryList />
              </div>
            </li>

            <li className="nav-item">
              <div className="nav-link">
                <span
                  onClick={() => this.onProfilePage(this.props.id)}
                  className="profile-link"
                >
                  <svg
                    width="1em"
                    height="1em"
                    viewBox="0 0 16 16"
                    className="bi bi-basket-fill basket-icon"
                    fill="currentColor"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fillRule="evenodd"
                      d="M5.071 1.243a.5.5 0 0 1 .858.514L3.383 6h9.234L10.07 1.757a.5.5 0 1 1 .858-.514L13.783 6H15.5a.5.5 0 0 1 .5.5v2a.5.5 0 0 1-.5.5H15v5a2 2 0 0 1-2 2H3a2 2 0 0 1-2-2V9H.5a.5.5 0 0 1-.5-.5v-2A.5.5 0 0 1 .5 6h1.717L5.07 1.243zM3.5 10.5a.5.5 0 1 0-1 0v3a.5.5 0 0 0 1 0v-3zm2.5 0a.5.5 0 1 0-1 0v3a.5.5 0 0 0 1 0v-3zm2.5 0a.5.5 0 1 0-1 0v3a.5.5 0 0 0 1 0v-3zm2.5 0a.5.5 0 1 0-1 0v3a.5.5 0 0 0 1 0v-3zm2.5 0a.5.5 0 1 0-1 0v3a.5.5 0 0 0 1 0v-3z"
                    />
                  </svg>
                  Мой профиль: {username}
                </span>
              </div>
            </li>
          </ul>
          <form className="form-inline my-2 my-lg-0">
            <input
              className="form-control mr-sm-2"
              type="search"
              placeholder="Search"
              aria-label="Search"
            />
            <button
              className="btn btn-outline-success my-2 my-sm-0"
              type="submit"
            >
              Search
            </button>
          </form>
        </div>
      </nav>
    );
  }
}

const putStateToProps = (state) => {
  return {
    isLogged: state.Auth.isLogged,
    username: state.Auth.user.username,
    id: state.Auth.user.id,
  };
};

const putActionToProps = (dispatch) => {
  return bindActionCreators(
    {
      USER_IS_LOGGED,
      getUserData,
    },
    dispatch
  );
};

export default connect(putStateToProps, putActionToProps)(withRouter(NavBar));
