const InitialState = {
  products: [],
  category: [],
  loading: true,
  detail_product: { title: "", images: [] },
};

const MainPageReducer = (state = InitialState, action) => {
  switch (action.type) {
    case "PRODUCTS_LOADING":
      return { ...state, loading: true };
    case "GET_PRODUCTS":
      return { ...state, products: action.data, loading: false };
    case "GET_CATEGORYLIST":
      return { ...state, category: action.data, loading: false };
    case "GET_CERTAIN_PRODUCT":
      console.log(action.data, "reducer");
      return {
        ...state,
        products: [],
        loading: false,
        detail_product: action.data,
      };
    default:
      return state;
  }
};

export default MainPageReducer;
