const InitialState = {
  access: "",
  refresh: "",
  isLoaded: false,
  isLogged: false,
  user: {},
  profileData: {},
};

const Auth = (state = InitialState, action) => {
  switch (action.type) {
    case "SUCCESS_LOGGED":
      return {
        ...state,
        isLogged: true,
      };
    case "FAIl_LOGGED":
      return {
        ...state,
        isLoaded: false,
      };
    case "USER_LOADED_SUCCESS":
      return {
        ...state,
        isLoaded: true,
      };
    case "USER_LOADED_FAIL":
      return {
        ...state,
        isLoaded: false,
      };
    case "SET_JWT":
      return {
        ...state,
        access: action.data.access,
        refresh: action.data.refresh,
      };
    case "GET_USER_DATA":
      return {
        ...state,
        user: action.data,
      };
    case "GET_PROFILE_DATA":
      return {
        ...state,
        profileData: action.data,
      };
    default:
      return state;
  }
};

export default Auth;
