import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";

import ApiManager from "../../service/store-service";

import { getListOfCategory } from "../actions/actions";
import Item from "./item";

class CategoryList extends Component {
  service = new ApiManager();

  componentDidMount() {
    this.service
      .getListOfItem("category")
      .then((data) => this.props.getListOfCategory(data));
  }
  render() {
    const { category } = this.props;
    return (
      <div className="cat-list" id="category">
        {category.map((item, index) => (
          <Item child={item} key={index} />
        ))}
      </div>
    );
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators({ getListOfCategory }, dispatch);
};

const putStateToProps = (state) => {
  return {
    category: state.MainPageReducer.category,
  };
};

export default connect(putStateToProps, putActionToProps)(CategoryList);
