const getListOfCategory = (data) => {
  return {
    type: "GET_CATEGORYLIST",
    data: data,
  };
};

const getListOfProducts = (data) => {
  return {
    type: "GET_PRODUCTS",
    data: data,
  };
};

const loadingItemList = () => {
  return {
    type: "PRODUCTS_LOADING",
  };
};

const getDetailPageOfItem = (data) => {
  return {
    type: "GET_CERTAIN_PRODUCT",
    data: data,
  };
};

const setJWTToken = (data) => {
  return {
    type: "SET_JWT",
    data: data,
  };
};

const USER_LOADED_SUCCESS = () => {
  return {
    type: "USER_LOADED_SUCCESS",
  };
};

const USER_LOADED_FAIL = () => {
  return {
    type: "USER_LOADED_FAIl",
  };
};

const USER_IS_LOGGED = () => {
  return {
    type: "SUCCESS_LOGGED",
  };
};

const USER_IS_NOT_LOGGED = () => {
  return {
    type: "FAIl_LOGGED",
  };
};

const getUserData = (data) => {
  return {
    type: "GET_USER_DATA",
    data: data,
  };
};

const getProfileData = (data) => {
  return {
    type: "GET_PROFILE_DATA",
    data: data,
  };
};

export {
  USER_LOADED_SUCCESS,
  getListOfCategory,
  getListOfProducts,
  loadingItemList,
  getDetailPageOfItem,
  setJWTToken,
  USER_LOADED_FAIL,
  USER_IS_LOGGED,
  getUserData,
  USER_IS_NOT_LOGGED,
  getProfileData,
};
