import React from "react";
import ApiManager from "../service/store-service";
import { bindActionCreators } from "redux";
import {
  setJWTToken,
  USER_IS_LOGGED,
  getUserData,
  USER_LOADED_SUCCESS,
  USER_IS_NOT_LOGGED,
} from "../components/actions/actions";
import { connect } from "react-redux";

import NavBar from "./header/navbar";
import ProductPage from "./pages/products/product-page";
import DetailProductPage from "./pages/detail-product/detail";
import Login from "./auth/login";
import Profile from "./pages/profile/profile";
import JWTWrapper from "./container";

import { Route } from "react-router-dom";

class App extends React.Component {
  api = new ApiManager();

  render() {
    return (
      <div>
        <NavBar />
        <Route path="/" component={ProductPage} exact />
        <Route
          path="/products/:id"
          render={({ match }) => {
            const { id } = match.params;
            return <DetailProductPage itemid={id} />;
          }}
          exact
        />
        <Route path="/login" component={Login} exact />
        <Route
          path="/profile/:id"
          render={({ match }) => {
            const { id } = match.params;
            return <Profile userId={id} />;
          }}
          exact
        />
      </div>
    );
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators(
    {
      setJWTToken,
      USER_IS_LOGGED,
      getUserData,
      USER_LOADED_SUCCESS,
      USER_IS_NOT_LOGGED,
    },
    dispatch
  );
};

export default connect(null, putActionToProps)(JWTWrapper(App));
