function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== "") {
    const cookies = document.cookie.split(";");
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      if (cookie.substring(0, name.length + 1) === name + "=") {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}

const JWTAuth = async (user_data) => {
  const csrftoken = getCookie("csrftoken");

  await fetch("http://127.0.0.1:8000/auth/jwt/create", {
    method: "POST",
    headers: { "Content-Type": "application/json", "x-csrftoken": csrftoken },
    body: JSON.stringify({
      username: user_data.username,
      password: user_data.password,
    }),
  })
    .then((result) => {
      return result.json();
    })
    .then((token) => {
      const access = token.access.replace(/"/g, "");
      const refresh = token.refresh.replace(/"/g, "");
      localStorage.setItem("jwtToken", JSON.stringify(access));
      localStorage.setItem("refreshToken", JSON.stringify(refresh));
    });
};

const setNewAccess = async (access) => {
  localStorage.setItem("jwtToken", access);
};

export { JWTAuth, setNewAccess };
