import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import "./style.css";

import ApiManager from "../../../../service/store-service";
import Error from "../../../error-indicator/error";

import { getListOfProducts, loadingItemList } from "../../../actions/actions";
import Spinner from "../../../spinner/spinner";
import ProductCard from "./item";

class ItemList extends Component {
  service = new ApiManager();
  componentDidMount() {
    const { loadingItemList, getListOfProducts } = this.props;
    loadingItemList();
    this.service
      .getListOfItem("products")
      .then((data) => getListOfProducts(data))
      .catch(<Error />);
  }
  render() {
    const { products, loading } = this.props;
    if (!loading) {
      return (
        <div className="row products-list">
          {products.map((item, index) => (
            <ProductCard key={index} data={item} />
          ))}
        </div>
      );
    }
    return <Spinner />;
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators({ getListOfProducts, loadingItemList }, dispatch);
};

const putStateToProps = (state) => {
  return {
    products: state.MainPageReducer.products,
    loading: state.MainPageReducer.loading,
  };
};

export default connect(putStateToProps, putActionToProps)(ItemList);
