import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import ApiManager from "../../../service/store-service";

import { getDetailPageOfItem, loadingItemList } from "../../actions/actions";
import Spinner from "../../spinner/spinner";
import "./style.css";

class DetailProductPage extends Component {
  service = new ApiManager();

  componentDidMount() {
    this.props.loadingItemList();
    this.service
      .getProductByID(this.props.itemid)
      .then((data) => this.props.getDetailPageOfItem(data));
  }

  render() {
    const settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
      arrows: false,
      autoplay: true,
    };
    const { detail_product, loading } = this.props;

    if (!loading) {
      return (
        <div className="row detail-block ">
          <div className="col-md-3 col-12 detail-image-block ">
            <Slider {...settings}>
              {detail_product.images.map((item, index) => (
                <img
                  src={`${item.image}`}
                  key={index}
                  className="detail-image"
                  alt={index}
                />
              ))}
            </Slider>
          </div>

          <div className="col-md-8 col-12">
            <h3>{detail_product.title}</h3>
            <p className="text-break">{detail_product.description}</p>
            <h4 className="text-primary">{detail_product.price}$</h4>
            <button className="btn btn-primary">Добавить в корзину</button>
          </div>
        </div>
      );
    } else {
      return <Spinner />;
    }
  }
}

const putActionToProps = (dispatch) => {
  return bindActionCreators({ getDetailPageOfItem, loadingItemList }, dispatch);
};

const putStateToProps = (state) => {
  return {
    detail_product: state.MainPageReducer.detail_product,
    loading: state.MainPageReducer.loading,
  };
};

export default connect(putStateToProps, putActionToProps)(DetailProductPage);
